          box-shadow: inset 0px -3px 0px 0px rgb(148, 148, 177);
  font-size: 30px;
  -webkit-transform: scaleY(.75);
      -ms-transform: scaleY(.75);
          transform: scaleY(.75);
}

#modal-close-button:focus {
